import deepmerge from 'deepmerge';
import conf from '../../config.json';

interface ExtendedConf extends Config {
  [key: string]: unknown;
}

const defaultConf: Config = {
  title: '',
  description: '',
  colors: {
    primary: '#000',
    secondary: '#fff',
  },
  socialLinks: [],
};

export const config: ExtendedConf = deepmerge(
  defaultConf,
  conf as Partial<Config>
);
